    <el-dialog
      title=""
      v-model="expenseDetailVisible"
      width="800px"
    >
      <div class="expense-detail-container">
        <!--  -->
        <el-table
          :data="expenseList"
          border
          size="small"
          style="width: 100%"
        >
          <el-table-column prop="expenseType" label="" width="120"></el-table-column>
          <el-table-column prop="amount" label="" width="120">
            <template #default="scope">
              {{ formatCurrency(scope.row.amount) }}
            </template>
          </el-table-column>
          <el-table-column prop="currency" label="" width="80"></el-table-column>
          <el-table-column prop="description" label=""></el-table-column>
          <el-table-column prop="createTime" label="" width="160"></el-table-column>
          <el-table-column prop="creator" label="" width="100"></el-table-column>
          <el-table-column label="" width="120" fixed="right">
            <template #default="scope">
              <el-button type="text" size="small" @click="handleEditExpense(scope.row)"></el-button>
              <el-button type="text" size="small" @click="handleDeleteExpense(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <div class="expense-form">
          <h4></h4>
          <el-form :model="expenseForm" label-width="100px">
            <el-form-item label="" required>
              <el-select v-model="expenseForm.expenseType" placeholder="" style="width: 100%">
                <el-option label="" value="transport"></el-option>
                <el-option label="" value="loading"></el-option>
                <el-option label="" value="storage"></el-option>
                <el-option label="" value="other"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" required>
              <el-input-number 
                v-model="expenseForm.amount" 
                :precision="2" 
                :step="0.1" 
                :min="0"
                style="width: 200px"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="" required>
              <el-select v-model="expenseForm.currency" placeholder="" style="width: 200px">
                <el-option label="" value="CNY"></el-option>
                <el-option label="" value="USD"></el-option>
                <el-option label="" value="EUR"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input
                v-model="expenseForm.description"
                type="textarea"
                :rows="3"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="expenseDetailVisible = false"></el-button>
          <el-button type="primary" @click="handleAddExpense"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="editExpenseVisible"
      width="500px"
    >
      <el-form :model="editExpenseForm" label-width="100px">
        <el-form-item label="" required>
          <el-select v-model="editExpenseForm.expenseType" placeholder="" style="width: 100%">
            <el-option label="" value="transport"></el-option>
            <el-option label="" value="loading"></el-option>
            <el-option label="" value="storage"></el-option>
            <el-option label="" value="other"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-input-number 
            v-model="editExpenseForm.amount" 
            :precision="2" 
            :step="0.1" 
            :min="0"
            style="width: 200px"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="" required>
          <el-select v-model="editExpenseForm.currency" placeholder="" style="width: 200px">
            <el-option label="" value="CNY"></el-option>
            <el-option label="" value="USD"></el-option>
            <el-option label="" value="EUR"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="editExpenseForm.description"
            type="textarea"
            :rows="3"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editExpenseVisible = false"></el-button>
          <el-button type="primary" @click="saveEditExpense"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  AppointmentModal  -->
    <AppointmentModal
      :visible="pickupModalVisible"
      :current-appointment="pickupAppointmentData"
      :edit-mode="pickupEditMode" 
      @save="handlePickupModalSave" 
      @close="closePickupModal"
      :min-date="null" 
      :available-time-slots="fixedTimeSlots" 
      :appointments="tableData" 
      :appointment-settings="mockAppointmentSettings"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted, watch} from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import FilterPanel from '@/components/logistics/FilterPanel.vue';
import GroupableTable from '@/components/logistics/GroupableTable.vue';
import ExpenseDetailDialog from '@/components/logistics/ExpenseDetailDialog.vue';
import HistoryRecordDialog from '@/components/logistics/HistoryRecordDialog.vue';
import GpsTrackDialog from '@/components/logistics/GpsTrackDialog.vue';
import VehicleAssignmentDialog from '@/components/logistics/VehicleAssignmentDialog.vue'
import UploadPodDialog from '@/components/logistics/UploadPodDialog.vue';
import FileUploadWithTagDialog from '@/components/logistics/FileUploadWithTagDialog.vue';
import AppointmentModal from '@/components/AppointmentModal.vue'; // Import AppointmentModal
import supplierDeliveryOrdersColumns from '@/assets/json/supplierDeliveryOrdersColumns.json';
import supplierDeliveryOrdersData from '@/assets/json/supplierDeliveryOrdersData.json';
import childTableColumns from '@/assets/json/childTableSupplier.json';
import { DArrowLeft, PictureFilled, Document } from '@element-plus/icons-vue';
import draggable from 'vuedraggable';

// 
const defaultTableColumns = supplierDeliveryOrdersColumns;

// JSON
const defaultData = supplierDeliveryOrdersData;

export default {
  name: 'SuppilerSeasOrder',
  components: {
    FilterPanel,
    GroupableTable,
    ExpenseDetailDialog,
    HistoryRecordDialog,
    GpsTrackDialog,
    draggable,
    DArrowLeft,
    VehicleAssignmentDialog,
    UploadPodDialog,
    FileUploadWithTagDialog,
    PictureFilled,
    Document,
    AppointmentModal, // Register AppointmentModal
  },
  setup() {
    // 
    const tableColumns = ref(defaultTableColumns);
    const childColumns = ref(childTableColumns);
    const tableData = ref(defaultData);
    const displayData = ref([]);
    const loading = ref(false);
    const visibleColumns = ref([...defaultTableColumns]);
    const selectedRows = ref([]);
    
    // 
    const warehouse = ref('west2');
    const createDateType = ref('');
    const dateRange = ref([]);
    const searchType = ref('');
    const searchText = ref('');
    const sendOrder = ref('no');
    const currentStatus = ref('all'); // 
    const filterPanelVisible = ref(false);
    const activeFilters = ref([]);
    const filterLogic = ref('and');
    
    // 
    const currentPage = ref(1);
    const pageSize = ref(20);
    const totalItems = ref(supplierDeliveryOrdersData.length);
    const total = computed(() => totalItems.value);
    
    // 
    const currentView = ref('all'); // 
    const selectedCustomView = ref(''); // ID
    const customViews = ref([]);
    const saveViewDialogVisible = ref(false);
    const viewOptionsDialogVisible = ref(false);
    const renameViewDialogVisible = ref(false);
    const groupField = ref('');
    const viewForm = ref({
      name: '',
      columns: []
    });
    const currentEditingView = ref(null); // 
    const renameForm = ref({
      name: ''
    });
    
    // 
    const groupModalVisible = ref(false);
    const groupSettings = ref({
      level1: '',
      level2: '',
      level3: ''
    });
    const hasGroupedData = computed(() => {
      return displayData.value.some(item => item.isGroup);
    });
    
    // 
    const getGroupTagType = (level) => {
      const types = ['', 'primary', 'success', 'warning'];
      return types[level] || '';
    };
    
    // 
    const loadingVisibleColumns = computed(() => 
      tableColumns.value.filter(col => [
        'trainNumber', 'loadingType', 'loadingStatus', 'loadingStartTime', 
        'loadingEndTime', 'loader', 'dockNumber', 'actualPallets'
      ].includes(col.prop))
    );
    
    const dispatchVisibleColumns = computed(() => 
      tableColumns.value.filter(col => [
        'trainNumber', 'deliveryType', 'supplier', 'driverPhone', 
        'licensePlate', 'destination', 'deliveryCost'
      ].includes(col.prop))
    );
    
    const driverVisibleColumns = computed(() => 
      tableColumns.value.filter(col => [
        'trainNumber', 'driverLicense', 'licensePlate', 'driverPhone',
        'loadingStatus', 'loadingAppointmentTime'
      ].includes(col.prop))
    );
    
    // 
    const groupableFields = computed(() => 
      tableColumns.value.filter(col => col.sortable && col.prop !== 'operation')
    );
    
    // 
    watch(currentView, (newView) => {
      // 
      if (['all', 'waitRegister', 'registered', 'loading', 'loaded', 'shipped', 'delivered', 'pod'].includes(newView)) {
        // 
        selectedCustomView.value = '';
        // 
        tableColumns.value.forEach(col => col.visible = true);
        // 
        activeFilters.value = [];
        filterLogic.value = 'and';
        // 
        groupSettings.value = { level1: '', level2: '', level3: '' };
        groupField.value = '';
        // 
        sortSettings.value = [{ field: '', direction: 'asc' }];
      } 
      // 
      else if (['loadingView', 'dispatchView', 'driverView'].includes(newView)) {
        // 
        selectedCustomView.value = '';
        // 
        if (newView === 'loadingView') {
          visibleColumns.value = loadingVisibleColumns.value;
        } else if (newView === 'dispatchView') {
          visibleColumns.value = dispatchVisibleColumns.value;
        } else if (newView === 'driverView') {
          visibleColumns.value = driverVisibleColumns.value;
        }
      }
      // IDcustomViewsID
      else {
        // 
        const customView = customViews.value.find(v => v.id === newView);
        if (customView) {
          // 
          selectedCustomView.value = newView;
          
          // 
          applyViewConfig(newView);
        }
      }
      
      // 
      applyFiltersAndPagination();
    });
    
    // 
    watch(selectedCustomView, (newView) => {
      // 
      if (newView) {
        currentView.value = newView;
      }
    });
    
    // 
    onMounted(() => {
      visibleColumns.value = tableColumns.value;
      loadSavedViews();
      fetchData();
      

    });
    

    
    // 
    const fetchData = () => {
      loading.value = true;
      try {
        // 
        tableData.value = supplierDeliveryOrdersData;
        totalItems.value = supplierDeliveryOrdersData.length;
        applyFiltersAndPagination();
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    };
    
    // 
    const applyFiltersAndPagination = () => {
      let filteredData = [...tableData.value];
      
      //  - 
      if (['waitRegister', 'registered', 'loading', 'loaded', 'shipped', 'delivered', 'pod'].includes(currentView.value)) {
        const statusMap = {
          'waitRegister': '',
          'registered': '',
          'loading': '',
          'loaded': '',
          'shipped': '',
          'delivered': '',
          'pod': 'POD'
        };
        
        const targetStatus = statusMap[currentView.value];
        if (targetStatus) {
          filteredData = filteredData.filter(item => item.loadingStatus === targetStatus);
        }
      }
      
      // 
      if (searchText.value) {
        filteredData = filteredData.filter(item => {
          const value = item[searchType.value];
          if (!value) return false;
          return value.toString().toLowerCase().includes(searchText.value.toLowerCase());
        });
      }
      
      // 
      if (dateRange.value && dateRange.value.length === 2) {
        const fieldMap = {
          'create': 'createdAt',
          'issue': 'issueDate',
          'appointment': 'loadingAppointmentTime'
        };
        
        const dateField = fieldMap[createDateType.value];
        const [startDate, endDate] = dateRange.value;
        
        filteredData = filteredData.filter(item => {
          const itemDate = item[dateField];
          if (!itemDate) return false;
          
          const dateOnly = itemDate.split(' ')[0];
          return dateOnly >= startDate && dateOnly <= endDate;
        });
      }
      
      // 
      if (activeFilters.value.length > 0) {
        filteredData = filteredData.filter(item => {
          const filterResults = activeFilters.value.map(filter => {
            const { field, operator, value } = filter;
            const itemValue = item[field];
            
            if (itemValue == null) {
              return false;
            }
            
            switch (operator) {
              case 'eq':
                return itemValue == value;
              case 'neq':
                return itemValue != value;
              case 'contains':
                return itemValue.toString().toLowerCase().includes(value.toString().toLowerCase());
              case 'notContains':
                return !itemValue.toString().toLowerCase().includes(value.toString().toLowerCase());
              case 'startsWith':
                return itemValue.toString().toLowerCase().startsWith(value.toString().toLowerCase());
              case 'endsWith':
                return itemValue.toString().toLowerCase().endsWith(value.toString().toLowerCase());
              case 'gt':
                return Number(itemValue) > Number(value);
              case 'gte':
                return Number(itemValue) >= Number(value);
              case 'lt':
                return Number(itemValue) < Number(value);
              case 'lte':
                return Number(itemValue) <= Number(value);
              default:
                return true;
            }
          });
          
          return filterLogic.value === 'and'
            ? filterResults.every(r => r)
            : filterResults.some(r => r);
        });
      }
      
      // 
      if (groupField.value) {
        filteredData = groupData(filteredData, groupField.value);
      }
      
      // 
      totalItems.value = filteredData.length;
      
      // 
      const start = (currentPage.value - 1) * pageSize.value;
      const end = start + pageSize.value;
      displayData.value = filteredData.slice(start, end);
    };
    
    // 
    const groupData = (data, field) => {
      const groups = {};
      
      data.forEach(item => {
        const value = item[field] || '';
        if (!groups[value]) {
          groups[value] = [];
        }
        groups[value].push({...item});
      });
      
      const result = [];
      for (const [key, items] of Object.entries(groups)) {
        result.push({
          id: `group-${key}`,
          groupName: key,
          count: items.length,
          isGroup: true,
          groupLevel: 0,
          children: items
        });
      }
      
      return result;
    };
    
    // 
    const handleSelectionChange = (rows) => {
      selectedRows.value = rows;
    };
    
    // 
    const getStatusTagType = (status) => {
      const statusMap = {
        '': 'info',
        '': 'warning',
        '': 'warning',
        '': 'success',
        '': 'success',
        '': 'success',
        'POD': 'success',
        '': 'danger',
        '': 'info',
        '': 'info'
      };
      
      return statusMap[status] || 'info';
    };
    
    // 
    const handleSaveView = () => {
      viewForm.value = {
        name: '',
        columns: tableColumns.value
          .filter(col => col.visible !== false)
          .map(col => col.prop)
      };
      saveViewDialogVisible.value = true;
    };
    
    const saveView = () => {
      if (!viewForm.value.name.trim()) {
        ElMessage.warning('');
        return;
      }
      
      const viewConfig = {
        id: Date.now().toString(),
        name: viewForm.value.name,
        columns: tableColumns.value.map(col => ({ 
          prop: col.prop, 
          visible: col.visible !== false 
        })),
        filters: activeFilters.value,
        filterLogic: filterLogic.value,
        groupSettings: groupSettings.value,
        sortSettings: sortSettings.value
      };
      
      customViews.value.push(viewConfig);
      selectedCustomView.value = viewConfig.id;
      saveViewDialogVisible.value = false;
      saveViewsToStorage();
      ElMessage.success('');
    };
    
    const handleDeleteView = () => {
      if (!currentEditingView.value) return;
      
      ElMessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const index = customViews.value.findIndex(v => v.id === currentEditingView.value.id);
      if (index !== -1) {
          customViews.value.splice(index, 1);
          if (currentView.value === currentEditingView.value.id) {
            currentView.value = 'all';
            selectedCustomView.value = '';
          }
          saveViewsToStorage();
          ElMessage.success('');
        }
        viewOptionsDialogVisible.value = false;
      }).catch(() => {
        viewOptionsDialogVisible.value = false;
      });
    };
    
    const loadSavedViews = () => {
      const saved = localStorage.getItem('deliveryTrainsViews');
      if (saved) {
        try {
          customViews.value = JSON.parse(saved);
        } catch (error) {
          console.error(':', error);
        }
      }
    };
    
    // 
    const handleDetail = (row) => {
      console.log('', row);
      // 
    };
    
    // 
    const handleCommand = (command, row) => {
      switch (command) {
        case 'pickup': // Add pickup case
          handlePickupAppointment(row); 
          break;
        case 'vehicleAssignment':
          openVehicleAssignment(row);
          break;
        case 'memo':
          handleMemo(row);
          break;
        case 'followUp':
          handleFollowUp(row);
          break;
        case 'history':
          handleHistory(row);
          break;
        case 'gpsTrack':
          handleGpsTrack(row);
          break;
        case 'fileUpload':
          handleFileUpload(row);
          break;
        case 'podUpload':
          handlePodUpload(row);
          break;
        case 'markException':
          handleMarkException(row);
          break;
        case 'fileUploadWithTag':
          handleFileUploadWithTag(row);
          break;
        case 'expenseDetail':
          handleExpenseDetail(row);
          break;
        case 'expensePull':
          handleExpensePull(row);
          break;
      }
    };

    // 
    const showFilterPanel = () => {
      filterPanelVisible.value = true;
    };

    // 
    const showAddTrainDialog = () => {
      // 
      console.log('showAddTrainDialog');
    };
    const expandedRowKeys = ref([]);
    // /
    const handleExpandChange = (row, expanded) => {
      // 
      if (expanded) {
        // 
        expandedRowKeys.value = [row.orderNumber];
      } else {
        // ,
        expandedRowKeys.value = [];
      }
    };

    // 
    const handleSortChange = ({ prop, order }) => {
      if (!prop || !order) {
        // 
        applyFiltersAndPagination();
        return;
      }
      
      // 
      const sortedData = [...displayData.value];
      
      // 
      sortedData.sort((a, b) => {
        let aValue = a[prop];
        let bValue = b[prop];
        
        // 
        if (!isNaN(aValue) && !isNaN(bValue)) {
          aValue = Number(aValue);
          bValue = Number(bValue);
        }
        
        // 
        if (typeof aValue === 'string' && typeof bValue === 'string') {
          if (order === 'ascending') {
            return aValue.localeCompare(bValue);
          } else {
            return bValue.localeCompare(aValue);
          }
        }
        
        // 
        if (order === 'ascending') {
          return aValue > bValue ? 1 : -1;
        } else {
          return aValue < bValue ? 1 : -1;
        }
      });
      
      displayData.value = sortedData;
    };

    // 
    const getGroupColumnLabel = (prop) => {
      const column = tableColumns.value.find(col => col.prop === prop);
      return column ? column.label : '';
    };
    
    // 
    const handleSizeChange = (size) => {
      pageSize.value = size;
      fetchData();
    };
    
    // 
    const handleCurrentChange = (page) => {
      currentPage.value = page;
      fetchData();
    };

    // 
    const handleSearch = () => {
      currentPage.value = 1;
      applyFiltersAndPagination();
    };

    // 
    const applyFilters = ({ filters, logic }) => {
      activeFilters.value = filters;
      filterLogic.value = logic;
      currentPage.value = 1;
      
      // 
      if (selectedCustomView.value) {
        updateViewConfig(selectedCustomView.value);
      }
      
      applyFiltersAndPagination();
    };

    // 
    const cancelFilters = () => {
      // 
    };

    // 
    const handleViewOptions = (view) => {
      currentEditingView.value = view;
      viewOptionsDialogVisible.value = true;
    };
    
    const handleRenameView = () => {
      if (!currentEditingView.value) return;
      
      renameForm.value.name = currentEditingView.value.name;
      viewOptionsDialogVisible.value = false;
      renameViewDialogVisible.value = true;
    };
    
    const saveRenameView = () => {
      if (!currentEditingView.value) return;
      if (!renameForm.value.name.trim()) {
        ElMessage.warning('');
        return;
      }
      
      const index = customViews.value.findIndex(v => v.id === currentEditingView.value.id);
      if (index !== -1) {
        customViews.value[index].name = renameForm.value.name;
        saveViewsToStorage();
        ElMessage.success('');
        renameViewDialogVisible.value = false;
      }
    };
    
    const handleCopyView = () => {
      if (!currentEditingView.value) return;
      
      const newView = {
        ...currentEditingView.value,
        id: Date.now().toString(),
        name: `${currentEditingView.value.name} `
      };
      
      customViews.value.push(newView);
      saveViewsToStorage();
      ElMessage.success('');
      viewOptionsDialogVisible.value = false;
    };

    // 
    const contextMenuVisible = ref(false);
    const contextMenuX = ref(0);
    const contextMenuY = ref(0);

    const handleContextMenu = (event, view) => {
      event.preventDefault();
      // 
      currentEditingView.value = view;
      contextMenuVisible.value = true;
      contextMenuX.value = event.clientX;
      contextMenuY.value = event.clientY;
      
      // 
      setTimeout(() => {
        document.addEventListener('click', closeContextMenu);
      }, 0);
    };
    
    const closeContextMenu = () => {
      contextMenuVisible.value = false;
      document.removeEventListener('click', closeContextMenu);
    };

    // 
    const editingCell = ref({
      rowIndex: -1,
      prop: '',
      value: ''
    });
    
    const editDialogVisible = ref(false);
    const editingRow = ref(null);
    const editingColumn = ref({});
    const editingForm = ref({
      value: ''
    });
    
    // 
    const handleCellClick = (row, column, cell) => {
      const prop = column.property;
      if (isEditableField(prop)) {
        const rowElement = cell ? cell.closest('tr') : null;
        const rowIndex = rowElement ? Array.from(rowElement.parentNode.children).indexOf(rowElement) : -1;
        
        editingCell.value = {
          rowIndex,
          prop,
          value: row[prop] || ''
        };
        
        setTimeout(() => {
          const input = document.querySelector('.cell-editor input, .cell-editor select');
          if (input) {
            input.focus();
          }
        }, 50);
      }
    };

    // 
    const getEditControlType = (prop) => {
      const column = tableColumns.value.find(col => col.prop === prop);
      return column?.type || 'input';
    };

    // 
    const getEditControlOptions = (prop) => {
      const column = tableColumns.value.find(col => col.prop === prop);
      return column?.options || [];
    };
    
    // 
    const handleCellDbClick = (row, column) => {
      const prop = column.property;
      if (isEditableField(prop)) {
        editingCell.value = {
          rowIndex: -1,
          prop: '',
          value: ''
        };
        
        editingRow.value = row;
        editingColumn.value = tableColumns.value.find(col => col.prop === prop) || { prop: prop, label: prop };
        editingForm.value.value = row[prop] || '';
        editDialogVisible.value = true;
      }
    };
    
    // 
    const saveEdit = (row, prop) => {
      if (editingCell.value.prop === prop) {
        row[prop] = editingCell.value.value;
        
        // API
        setTimeout(() => {
          // API
          // axios.post('/api/update', { id: row.id, [prop]: editingCell.value.value })
          
          // 
          const index = tableData.value.findIndex(item => item.id === row.id);
          if (index !== -1) {
            tableData.value[index] = { ...row };
          }
          
          editingCell.value = {
            rowIndex: -1,
