    // 
    const getOptionsForColumn = (prop) => {
      // 
      const columnDef = tableColumns.value.find(col => col.prop === prop);
      
      // filterOptions
      if (columnDef && columnDef.filterOptions) {
        return columnDef.filterOptions;
      }
      
      // filterOptions
      switch (prop) {
        case 'loader':
