            '09:00:00': { floor: 3, pallet: 1 },
            '10:00:00': { floor: 3, pallet: 1 },
            '11:00:00': { floor: 0, pallet: 2 }, //  11 
            '14:00:00': { floor: 4, pallet: 0 }, //  14 
            '15:00:00': { floor: 4, pallet: 2 }
          }
       },
       '2024-05-30': { // 
            isOpen: true,
            timeSlots: {
              '08:00:00': { floor: 2, pallet: 2 },
              '09:00:00': { floor: 2, pallet: 2 },
              '10:00:00': { floor: 1, pallet: 1 }
            }
       },
       '2024-05-31': { isOpen: false, timeSlots: {} } // 
    });
    // ---  ---

    // ---  ---
    const fixedTimeSlots = ref([
      '08:00:00', '09:00:00', '10:00:00', '11:00:00', 
      '13:00:00', '14:00:00', '15:00:00', '16:00:00', '17:00:00'
      // 
    ]);
    // ---  ---

    return {
      // 
      tableColumns,
      childColumns,
      tableData,
      displayData,
      loading,
      visibleColumns,
      selectedRows,
      
      // 
      warehouse,
      createDateType,
      dateRange,
      searchType,
      searchText,
      sendOrder,
      currentStatus,
      filterPanelVisible,
      activeFilters,
      filterLogic,
      
