      applyGroupSettings,
      
      // 
      handleSaveView,
      saveView,
      handleDeleteView,
      loadSavedViews,
      getStatusTagType,
      handleDetail,
      handleCommand,
      showFilterPanel,
      showAddTrainDialog,
      handleExpandChange,
      handleSelectionChange,
      handleSortChange,
      handleSizeChange,
      handleCurrentChange,
      handleSearch,
      applyFilters,
      cancelFilters,
      fetchData,
      applyFiltersAndPagination,
      groupData,
      handleViewOptions,
      handleRenameView,
      saveRenameView,
      handleCopyView,
      contextMenuVisible,
      contextMenuX,
      contextMenuY,
      handleContextMenu,
      closeContextMenu,
      
      // 
      editingCell,
      editDialogVisible,
      editingRow,
      editingColumn,
      editingForm,
      handleCellClick,
      handleCellDbClick,
      saveEdit,
      saveDialogEdit,
      getOptionsForColumn,
      getGroupColumnLabel,
      
      // 
      sortModalVisible,
      sortSettings,
      sortableFields,
      showSortModal,
