<template>
  <el-dialog
    title=""
    v-model="dialogVisible"
    width="900px"
    :close-on-click-modal="false"
  >
    <!--  -->
    <div class="search-area">
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="">
          <el-input
            v-model="searchForm.plateNumber"
            placeholder=""
            clearable
            @keyup.enter="handleSearch"
          />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchForm.status" placeholder="" clearable>
            <el-option label="" value="busy" />
            <el-option label="" value="idle" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
          <el-button @click="resetSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="plateNumber" label="" width="120" />
      <el-table-column prop="driver" label="" width="100" />
      <el-table-column prop="driverPhone" label="" width="120" />
      <el-table-column prop="status" label="" width="100">
        <template #default="scope">
          <el-tag :type="scope.row.status === 'busy' ? 'danger' : 'success'">
            {{ scope.row.status === 'busy' ? '' : '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="gpsNumber" label="GPS" width="120" />
      <el-table-column label="" width="150" fixed="right">
        <template #default="scope">
          <el-button
            v-if="scope.row.status === 'idle'"
            type="primary"
            size="small"
            @click="handleAssign(scope.row)"
          >
            
          </el-button>
          <el-button
            v-else
            type="danger"
            size="small"
            @click="handleCancelAssign(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        v-model:current-page="currentPage"
        v-model:page-size="pageSize"
        :page-sizes="[10, 20, 50, 100]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </el-dialog>
</template>

<script>
import { ref, reactive, watch } from 'vue'
import { ElMessageBox } from 'element-plus'

export default {
  name: 'VehicleAssignmentDialog',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'assign', 'cancel-assign'],
  setup(props, { emit }) {
    const dialogVisible = ref(props.modelValue)
    const loading = ref(false)
    const currentPage = ref(1)
    const pageSize = ref(10)
    const total = ref(100)

    // 
    const searchForm = reactive({
      plateNumber: '',
      status: ''
    })

    // 
    const mockData = Array.from({ length: 100 }, (_, index) => ({
      id: index + 1,
      plateNumber: `B${String(Math.floor(Math.random() * 10000)).padStart(5, '0')}`,
      driver: ['', '', '', ''][Math.floor(Math.random() * 4)],
      driverPhone: `138${String(Math.floor(Math.random() * 100000000)).padStart(8, '0')}`,
      status: Math.random() > 0.5 ? 'busy' : 'idle',
      gpsNumber: `GPS${String(Math.floor(Math.random() * 1000)).padStart(3, '0')}`
    }))

    const tableData = ref(mockData)

    // 
    watch(() => props.modelValue, (val) => {
      dialogVisible.value = val
    })

    watch(() => dialogVisible.value, (val) => {
      emit('update:modelValue', val)
    })

    // 
    const handleSearch = () => {
      loading.value = true
      // 
      setTimeout(() => {
        const filteredData = mockData.filter(item => {
          const matchPlate = !searchForm.plateNumber || 
            item.plateNumber.includes(searchForm.plateNumber)
          const matchStatus = !searchForm.status || 
            item.status === searchForm.status
          return matchPlate && matchStatus
        })
        tableData.value = filteredData
        total.value = filteredData.length
        loading.value = false
      }, 500)
    }

    // 
    const resetSearch = () => {
      searchForm.plateNumber = ''
      searchForm.status = ''
      handleSearch()
    }

    // 
    const handleAssign = (row) => {
      ElMessageBox.confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).then(() => {
        // 
        row.status = '';
        
        // 
        emit('assign', row);
      }).catch(() => {
        // 
      });
    };

    // 
    const handleCancelAssign = (row) => {
      ElMessageBox.confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).then(() => {
        // 
        row.status = '';
        
        // 
        emit('cancel-assign', row);
      }).catch(() => {
        // 
      });
    };

    // 
    const handleSizeChange = (val) => {
      pageSize.value = val
      handleSearch()
    }

    const handleCurrentChange = (val) => {
      currentPage.value = val
      handleSearch()
    }

    return {
      dialogVisible,
      loading,
      searchForm,
      tableData,
      currentPage,
      pageSize,
      total,
      handleSearch,
      resetSearch,
      handleAssign,
      handleCancelAssign,
      handleSizeChange,
      handleCurrentChange
    }
  }
}
</script>

<style scoped>
.search-area {
  margin-bottom: 20px;
}

.search-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style> 